import Calculator from "../calculator/calculator";
import LoanDetails from "../loan_details/loan_details";
import {useState} from "react";
import Landing from "../landing/landing";
import {QueryClient} from "react-query";
import Layout from "../landing/layout";

// for react query -- Create a client
new QueryClient();

function Home(props) {
    const [visibility] = useState(props.visibility);
    const [membershipProp] = useState("no")
    const [calculatorProps, setCalculatorProps] = useState([])
    const [isSuccess, setIsSuccess] = useState()
    const [isError, setIsError] = useState()
    const [isLoading, setIsLoading] = useState()

    const [data, setData] = useState([])
    const [showOptions, setShowOptions] = useState(false)
    const [user] = useState({
        fname: "",
        sname: "",
        phoneNumber: "",
        emailAddress: "",
        omang: "",
        dob: "",
    })

    return (
        <div className="App">
            {visibility ? (
                <div className="calculator-section">
                    {visibility.existingCustomer || visibility.newCustomer ?
                        <Calculator
                            setData={setData}
                            setShowOptions={setShowOptions}
                            setCalculatorProps={setCalculatorProps}
                            setIsLoading={setIsLoading}
                            setIsSuccess={setIsSuccess}
                            setError={setIsError}
                        /> : null}
                </div>
            ) : null}

            {visibility.existingCustomer || visibility.newCustomer ? (
                // <2 visibility={visibility} showOptions={showOptions} info={info} data={data}/>
                <LoanDetails
                    visibility={visibility}
                    showOptions={showOptions}
                    data={data}
                    membershipProp={membershipProp}
                    user={user}
                    calculatorProps={calculatorProps}
                    isLoading={isLoading}
                    isError={isError}
                    isSuccesful={isSuccess}
                />

            ) : (
                <Layout className="home-section">
                    <Landing/>
                </Layout>
            )}
            {/* {<Footer />} */}
        </div>
    );
}

export default Home;
