import React from 'react';
import '../loan_details/loan_details.css'


function SelectedLoan(props) {

    const {
        visibility,
        data,
        agreement,
        setAggrement,
        setEmploymentDetails,
        employmentDetails,
        showDialog,
        user,
        index
    } = props


    return (
        <div className="loan-details">
            {user.fname==="" ? <h3>Loan Details</h3>:<h3>{user.fname}, Loan Details</h3>}
            <div className="loan-details-values">
                <div className="loan-details-value">
                    <span>Maximum Amount</span>
                    <span>BPW<input type="text"
                                    value={data[index].maxLoan}
                                    readOnly="true"
                                    placeholder="00000"

                    /> </span>
                </div>
                <hr />
                <div className="loan-details-value">
                    <span>Take Home</span>
                    <span>BPW<input type="text"
                                    value={data[index].TakeHome}
                                    readOnly="true"
                                    placeholder="00000"
                    /> </span>
                </div>
                <hr />
                <div className="loan-details-value">
                    <span>
                        Affordability
                    </span>
                    <span>BPW<input readOnly="true" type="text" placeholder="BWP00000"
                                 value={data[index].affordabity}/></span>
                </div>
                <hr/>
                <div className="loan-details-value">
                    <span>Rate</span>
                    <span><input readOnly="true" type="text" placeholder="0 %"
                                 value={data[index].rate}/> </span>
                </div>
                <hr/>
                 <div className="loan-details-value">
                        <span>Loan Term</span>
                     <span><input readOnly="true" type="text" placeholder="0"
                                  value={data[index].LoanTerm}/> </span>
                    </div>
                <hr/>
                <br/>
                <div style={{textAlign: 'left', fontSize: '8px', color: 'red'}}>
                    <p>**This application serves as only a quotation and is therefore non-binding</p>
                </div>
                <br/>
                <div>
                    <div>
                        <input type='checkbox' checked={agreement} onChange={() => setAggrement(!agreement)}/> I
                        have read and understood the terms and conditions
                    </div>
                    {agreement ? <div className="buttons">
                        <div className="button-proceed button-space">
                            <p>Change</p>
                        </div>
                        {visibility.newCustomer ? (<div className="button-proceed button-space"
                                                        onClick={() => setEmploymentDetails(!employmentDetails)}>
                            <p>Proceed</p>
                        </div>) : (<div className="button-proceed button-space"
                                        onClick={() => showDialog()}>
                            <p>Submit</p>
                        </div>)
                        }
                    </div> : null}
                </div>

            </div>
        </div>
    );
}

export default SelectedLoan;