import React from 'react'
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Button from 'react-bootstrap/Button';


function LoanDetailsTable(props) {
    const data = props.data

    function handleOnclick(index) {
        props.setIndex(index)
    }

    // Render the UI for your table
    return (
        <TableContainer
        >
            <Table>

                <TableHead>
                    <TableRow>
                        <TableCell>Option</TableCell>
                        <TableCell align="right">Affordability</TableCell>
                        <TableCell align="right">Rates&nbsp;(%)</TableCell>
                        <TableCell align="right">Loan Term</TableCell>
                        <TableCell align="right">Maximum Amount</TableCell>
                        <TableCell align="right">Take Home</TableCell>
                        <TableCell></TableCell>
                    </TableRow>
                </TableHead>

                <TableBody>
                    {data?.map((row, index) => {
                        const {affordabity, rate, LoanTerm, maxLoan, TakeHome} = row
                        return (
                            <TableRow key={row.name}>
                                <TableCell component="th" scope="row">
                                    {index + 1}
                                </TableCell>
                                <TableCell align="center">{affordabity}</TableCell>
                                <TableCell align="center">{rate}</TableCell>
                                <TableCell align="center">{LoanTerm}</TableCell>
                                <TableCell align="center">{maxLoan}</TableCell>
                                <TableCell align="center">{TakeHome}</TableCell>
                                <TableCell>
                                    <Button
                                        variant="outline-dark"
                                        onClick={() => handleOnclick(index)}
                                    >Select
                                    </Button>
                                </TableCell>
                            </TableRow>
                        )
                    })}
                </TableBody>

            </Table>


        </TableContainer>
    )
}

export default LoanDetailsTable