import "./calculator.css";
import {useState, useEffect} from "react";
import {Api} from "../../tools/api";
import {useMutation} from "react-query";
import TextField from '@material-ui/core/TextField';
import InputAdornment from "@material-ui/core/InputAdornment";
import {CircularProgress, MenuItem, Select} from "@material-ui/core";
import {Button, Form} from "react-bootstrap";
import Alert from 'react-bootstrap/Alert'


export default function Calculator({
                                       setData,
                                       setShowOptions,
                                       setCalculatorProps,
                                       setIsLoading,
                                       setError,
                                       setIsSuccess
                                   }) {
    const [toggleRO] = useState(false);
    const [inputs, setInputs] = useState({
        BasicSalary: 0,
        Employer: '', NetPay: 0,
        MaritalStatus: '',
        TotalFixedAllowances: 0,
        GrossSalary: 0,
    });

    // initialise api class
    const [api] = useState(new Api());
    // for getting loan calculation
    const {
        mutate,
        data,
        isLoading,
        isSuccess,
        isError,
        error
    } = useMutation('calculateLoan', api.calculateLoan, {retry: 2});

    // handle states from mutation
    useEffect(() => {
        // @TODO -- change to alerts and remove success
        if (isLoading) {
            setIsLoading(isLoading)
            console.log("isLoading",isLoading)
        };

        if (isSuccess) {
            console.log("isSuccess",isSuccess)
            setIsSuccess(isSuccess)
            if (data) setShowOptions(true);
            console.log("data",data)
            setData(data);
            setCalculatorProps(inputs)
            return <Alert variant={'success'}>Successful!</Alert>;
        }

        if (isError && error) console.log(isError);
    }, [isLoading, isError, isSuccess, setIsLoading, error, setError, setIsSuccess, data, setShowOptions, setData, setCalculatorProps, inputs])


    const onSubmit = (e) => {
        mutate(inputs);
        e.preventDefault();
    }

    return (
        <div className="calculator">
            <h3>Get Your Loan Quotation</h3>
            <div className="calculator-values">
                <Form onSubmit={(e) => onSubmit(e)}>
                    <div className="value">
                        <span>Employer</span>
                        <span>
                        <Select
                            style={{width: '200px'}}
                            labelId="demo-simple-select-label"
                            id="employer"
                            className="textField"
                            defaultValue={'Central Government'}
                            value={inputs.Employer}
                            required={true}
                            onChange={(e) =>
                                setInputs({...inputs, Employer: e.target.value})}
                        >
                          <MenuItem style={{fontFamily:'Myriad Pro',}} value={"Government of Botswana"}>Government of Botswana</MenuItem>
                          <MenuItem style={{fontFamily:'Myriad Pro',}} value={"Councils/Lands Boards"}>Councils/Lands Boards</MenuItem>
                          <MenuItem style={{fontFamily:'Myriad Pro',}} value={"Bamalete/KSDA"}>Bamalete/KSDA</MenuItem>
                          <MenuItem style={{fontFamily:'Myriad Pro',}} value={"Private"}>Private</MenuItem>
                        </Select>
                    </span>
                    </div>
                    <div className="value">
                        <span>Marital Status</span>
                        <span>
                        <Select
                            style={{width: '200px'}}
                            labelId="demo-simple-select-label"
                            id="maritalStatus"
                            className="textField"
                            defaultValue={'Single'}
                            required={true}
                            value={inputs.MaritalStatus}
                            onChange={(e) =>
                                setInputs({...inputs, MaritalStatus: e.target.value})}
                        >
                            <MenuItem style={{fontFamily:'Myriad Pro',}} value={"Married"}>Married</MenuItem>
                            <MenuItem style={{fontFamily:'Myriad Pro',}} value={"Single"}>Single</MenuItem>
                        </Select>
                    </span>
                    </div>
                    <div className="value">
                        <span>Gross Salary</span>
                        <span><TextField
                            id="gross_salary"
                            className="textField"
                            required={true}
                            value={inputs.GrossSalary}
                            type="integer"
                            style={{fontFamily:'Myriad Pro',}}
                            onChange={e => setInputs({...inputs, GrossSalary: e.target.value})}
                            InputProps={{
                                inputProps: {min: 2000, max: 500000},
                                startAdornment: <InputAdornment position="start">BWP</InputAdornment>,
                            }}
                        /></span>
                    </div>
                    <div className="value">
                        <span>Net Pay</span>
                        <span><TextField
                            id="net_pay"
                            className="textField"
                            type="integer"
                            required={true}
                            style={{fontFamily:'Myriad Pro',}}
                            value={inputs.NetPay}
                            onChange={e => setInputs({...inputs, NetPay: e.target.value})}
                            InputProps={{
                                startAdornment: <InputAdornment position="start">BWP</InputAdornment>,
                            }}
                        /></span>
                    </div>
                    <div className="value">
                        <span>Basic Salary</span>
                        <span><TextField
                            id="basic_salary"
                            className="textField"
                            type="integer"
                            required={true}
                            style={{fontFamily:'Myriad Pro',}}
                            value={inputs.BasicSalary}
                            onChange={e => setInputs({...inputs, BasicSalary: e.target.value})}
                            InputProps={{
                                inputProps: {min: 2000.00, max: 500000.00},
                                startAdornment: <InputAdornment position="start">BWP</InputAdornment>,
                            }}
                        /></span>
                    </div>
                    <div className="value">
                        <span>Total Fixed Allowances</span>
                        <span><TextField
                            id="total_fixed_allowances"
                            className="textField"
                            type="integer"
                            required={true}
                            style={{fontFamily:'Myriad Pro',}}
                            value={inputs.TotalFixedAllowances}
                            onChange={e => setInputs({...inputs, TotalFixedAllowances: e.target.value})}
                            InputProps={{
                                startAdornment: <InputAdornment position="start">BWP</InputAdornment>,
                            }}
                        /></span>
                    </div>
                    <Button style={{
                        marginTop: '20px',
                        marginRight: '15px',
                        width: '90%',
                        height: '30%',
                        padding: '10px',
                        backgroundColor: '#010f3e',
                        color: 'white',
                        borderRadius: '5px',
                        border: 'solid 1px #010f3e',
                        fontSize: 'large',
                        textAlign: 'center',
                        fontFamily:'Myriad Pro',
                    }}
                            className='button-gen'
                            type="submit">
                        {
                            isLoading ? <CircularProgress style={{
                                    display: 'flex',
                                    margin: '0 auto',
                                    alignSelf: "center",
                                    height: '40px',
                                    width: "40px",
                                    color: "#00b0ed"
                                }} type="bars"/> :
                                (isSuccess || isError || !isLoading) && `GET A QUOTE `
                        }

                    </Button>
                    {!toggleRO && (
                        <div style={{textAlign: "center", fontSize: "8px", color: "red"}}>
                            <p>
                                **This is a no obligation quote
                            </p>
                        </div>
                    )}
                </Form>
            </div>
        </div>
    );
}
