import {useState, useEffect} from 'react';
import './loan_details.css'
import Modal from "react-modal";
import LoanOptions from "../loan_options/loan_options";
import SelectedLoan from "../tables/selected_loan";
import {useMutation} from "react-query";
import {Api} from '../../tools/api'
import {Form, Button} from "react-bootstrap";

export default function LoanDetails({
                                        visibility, info, membershipProp, showOptions,
                                        data, calculatorProps, user,
                                    }) {
    // get change info from loan context -- used with inputs
    const [employmentDetails, setEmploymentDetails] = useState(false)
    const [agreement, setAggrement] = useState(false)
    const [index, setIndex] = useState();
    const [isOpen, setIsOpen] = useState(false);
    const [userDetails, setUserDetails] = useState({
        fname: '',
        sname: '',
        phoneNumber: '',
        email: '',
        omang: '',
        dob: '',
    })

    const [api] = useState(new Api());

    // saving client to db
    const {
        mutate: sendSMS,
        isSuccess: sendSMSSuccess,
    } = useMutation('sendSMS', api.sendSMS, {retry: 2});

    const {
        mutate: addApp,
        isSuccess: addAppSuccess,
    } = useMutation('addLoanApplication', api.addLoanApplication, {retry: 2});

    const {
        mutate: saveClient
    } = useMutation('calculateLoan', api.addClient, {retry: 2});

    useEffect(()=>{
        if (sendSMSSuccess && addAppSuccess) {
            // setIsOpen(true);
        }
    },[sendSMSSuccess,addAppSuccess ])

    useEffect(() => {
        if (membershipProp === "yes") {
            setUserDetails({
                fname: user.fname,
                sname: user.sname,
                phoneNumber: user.phoneNumber,
                email: user.email,
                omang: user.omang,
                dob: user.dob,
            })
        }
    }, [membershipProp, user.fname, user.sname, user.phoneNumber, user.email, user.omang, user.dob], )

    const showDialog = () => {
        setIsOpen(!isOpen);
        window.location.reload(false);
    }

    const handleSubmit = (e) => {
        e.preventDefault();
        const params = {
            recipient: userDetails.phoneNumber,
            message: "Your application has been received. Our representatives will contact you as soon as possible.",
        }
        // save client to db
        const client = {
            ...userDetails, ...calculatorProps
        }
        saveClient(client)
        // save loan application
        const app = {
            allLoanOptions: data,
            chosenLoanOption: data[index],
            ...client,
            appliedOn: new Date(),
            status: 'pending',
            resolvedOn: '',
            resolvedBy: '',
        }
        addApp(app)
        sendSMS(params)
        setIsOpen(!isOpen)
    }

    return (
        <section className="site-root">
            {(showOptions && data) ?
                <LoanOptions className="loan-options" visibility={visibility} showOptions={showOptions} info={info} data={data}
                             setIndex={setIndex}/> : null

            }
            {index>-1 ?
                <SelectedLoan
                    info={info}
                    visibility={visibility}
                    showOptions={showOptions}
                    data={data}
                    agreement={agreement}
                    setAggrement={setAggrement}
                    employmentDetails={employmentDetails}
                    setEmploymentDetails={setEmploymentDetails}
                    showDialog={showDialog}
                    user={user}
                    index={index}

                /> :
                (!showOptions) &&
                <div>
                    <span className="banner_text">A loan to suit your needs </span>
                    <div id="arrowAnim">
                        <div className="arrowSliding">
                            <div className="arrow"/>
                        </div>
                        <div className="arrowSliding delay1">
                            <div className="arrow"/>
                        </div>
                        <div className="arrowSliding delay2">
                            <div className="arrow"/>
                        </div>
                        <div className="arrowSliding delay3">
                            <div className="arrow"/>
                        </div>
                    </div>
                </div>
            }

            {visibility.newCustomer && employmentDetails &&
            <div className="personal-details">
                <Form onSubmit={e => handleSubmit(e)}>
                    <h3>Personal Details</h3>
                    <div className="personal-details-values">
                        <input type="text" placeholder="First Names" value={userDetails.fname}
                               onChange={(e) => setUserDetails({...userDetails, fname: e.target.value})}/>
                        <input type="text" placeholder="Surname" value={userDetails.sname}
                               onChange={(e) => setUserDetails({...userDetails, sname: e.target.value})}/>
                    </div>
                    <div className="personal-details-values">
                        <input type="text" placeholder="Phone Number" value={userDetails.phoneNumber}
                               onChange={(e) => setUserDetails({...userDetails, phoneNumber: e.target.value})}/>
                        <input type="email" placeholder="Email Address"
                               value={userDetails.email}
                               onChange={(e) => setUserDetails({...userDetails, email: e.target.value})}/>
                    </div>
                    <div className="personal-details-values">
                        <input type="text" placeholder="ID Number/ Passport"
                               value={userDetails.omang}
                               onChange={(e) => setUserDetails({...userDetails, omang: e.target.value})}/>
                        <input type="date" placeholder="Date of birth"
                               value={userDetails.dob}
                               onChange={(e) => setUserDetails({...userDetails, dob: e.target.value})}/>
                    </div>

                    <br/>
                    <div className="button-proceed button-space"
                         style={{
                             display: 'flex',
                             justifyContent: 'center'
                         }}>
                        <Button style={{
                            margin: "10px auto",
                            width: '20%',
                            height: '30%',
                            padding: '10px',
                            backgroundColor: '#010f3e',
                            color: 'white',
                            borderRadius: '5px',
                            border: 'solid 1px #010f3e',
                            fontSize: 'large',
                            textAlign: 'center',
                        }} type="submit">
                            Submit
                        </Button>
                    </div>
                </Form>
            </div>
            }
            <Modal
                isOpen={isOpen}
                onRequestClose={showDialog}
                contentLabel="My dialog"
                className="modal"
                overlayClassName="myoverlay"
            >

                <div className="membership-modal">
                    <div className="close" onClick={(e) => showDialog}>close</div>
                    <div style={{textAlign: 'center'}} className="title">
                        <h1>
                            Thank you for your interest in a personal loan. A call center agent will contact you soon.
                        </h1>
                    </div>
                    <br/>
                    <div className="button-proceed button-space"
                         style={{
                             display: 'flex',
                             justifyContent: 'center'
                         }}>
                        <Button style={{
                            margin: "10px auto",
                            width: '20%',
                            height: '30%',
                            padding: '10px',
                            backgroundColor: '#010f3e',
                            color: 'white',
                            borderRadius: '5px',
                            border: 'solid 1px #010f3e',
                            fontSize: 'large',
                            textAlign: 'center',
                        }} onClick={showDialog}>
                            Done
                        </Button>
                    </div>
                </div>
            </Modal>
        </section>
    );
}