import Header from "./components/header/header";
import {useEffect, useState} from "react";
import {LoanContext} from "./context";
import {QueryClient, QueryClientProvider} from "react-query";
import {BrowserRouter as Router, Route, Switch} from 'react-router-dom'
import Home from '../src/components/Pages/home'
import ContactUs from "./components/Pages/contact_us";
import AboutUs from "./components/Pages/about_us";
import FAQS from "./components/Pages/f_a_q_s";
import Footer from "./components/footer/footer";
import './App.css'
import './components/footer/footer.css'
import TAnC from "./components/Pages/t_an_c";
import Privacy from "./components/Pages/privacy";


// for react query -- Create a client
const queryClient = new QueryClient();

function App() {
    const [visibility, setVisibility] = useState({
        newCustomer: false,
        existingCustomer: false
    });
    const [membershipProp, setMembershipProp] = useState("no")
    const [user, setUser] = useState([])

    const [info, setInfo] = useState(
        {
            loanNeeded: 0,
            monthsToPay: 1,
            grossSalary: 0,
            netPay: user.monthlyNetIncome,
            basicSalary: 0,
            totalFixedAllowances: 0,
            nextPaymentDate: Date("01/01/2022"),
            monthlyLoanRepayment: 0,
            nextPaymentDateAt: Date("01/01/2022"),
            employer: user.employer,
            names: user.fname,
            surname: user.sname,
            phoneNumber: user.phoneNumber,
            emailAddress: user.email,
            omang: user.omang,
            dob: user.dob,
            maritalStatus: user.maritalStatus
        });

    useEffect(() => {
        if (membershipProp === "no") {
            setUser({
                fname: "",
                email: "",
                phoneNumber: "",
                sname: "",
                maritalStatus: "",
                employer: "",
                monthlyNetIncome: 0,
                omang: "",
                dob: ""
            })
        }
    }, [membershipProp]);

    const changeInfo = obj => {
        setInfo(prev => {
            return {
                ...prev,
                ...obj
            };
        });
    };

    const toggleInvisibleExisting = () => {
        setVisibility(prev => {
            return {
                ...prev,
                existingCustomer: true
            };
        });
    };

    const toggleInvisibleNew = () => {
        setVisibility(prev => {
            return {
                ...prev,
                newCustomer: true
            };
        });
    };

    //@TODO check for usages and remove -- preferably use within individual components for mutations
    const HomeWithProps = () => {
        return <Home visibility={visibility}/>
    }

    return (
        // wrap everything in react query provider
        <QueryClientProvider client={queryClient}>
            <LoanContext.Provider value={{...info, changeInfo}}>
                <Router>
                    <div className="App">
                        <Header
                            toggleInvisibleExisting={toggleInvisibleExisting}
                            toggleInvisibleNew={toggleInvisibleNew}
                            setMembershipProp={setMembershipProp}
                            setUser={setUser}
                        />
                    </div>
                    <Switch>
                        <Route exact path="/" component={HomeWithProps}/>
                        <Route exact path="/about_us" component={AboutUs}/>
                        <Route exact path="/contact_us" component={ContactUs}/>
                        <Route exact path="/faqs" component={FAQS}/>
                        <Route exact path="/tanc" component={TAnC}/>
                        <Route exact path="/privacy" component={Privacy}/>
                    </Switch>
                    <Footer/>
                </Router>
            </LoanContext.Provider>
        </QueryClientProvider>
    );
}

export default App;
