import "./header.css";
import React, {useRef, useState, useEffect} from "react";
import bayportLogo from "./bayportlogo.png";
import background from "./cover_image.png";
import {useMutation} from "react-query";
import {Api} from "../../tools/api";
import {generateRandomLetters, generateRandomNumber} from "../../tools/functions";
import Button from '@mui/material/Button';
import Dialog from '@mui/material/Dialog';
import DialogActions from '@mui/material/DialogActions';
import DialogContent from '@mui/material/DialogContent';
import DialogContentText from '@mui/material/DialogContentText';
import DialogTitle from '@mui/material/DialogTitle';
import '@fontsource/roboto/500.css';
import {Card, TextField, Typography} from "@material-ui/core";
import {Toast} from "react-bootstrap";
import Navbar from "./navbar";
import Sidebar from "./sidebar";
import {useHistory} from "react-router-dom";

export default function Header({
                                   toggleInvisibleExisting,
                                   toggleInvisibleNew,
                                   setMembershipProp,
                                   setUser
                               }) {
    const [isOpen, setIsOpen] = useState(false);

    const [cycle, setStateCycle] = useState("start");

    const [membership, setMembership] = useState("no");

    const [, setButtonVisible] = useState(true);

    const [maxWidth] = React.useState('sm');

    const applyButton = useRef();

    const history = useHistory();

    // initialise the api
    const [api] = useState(new Api());
    // stores OTP ID and OTP code when being sent
    const [otp, setOTP] = useState({id: '', code: ''});
    // stores user inputs from modal
    const [inputs, setInputs] = useState({phone: ' ', enteredOTP: ' ', omang: ' '});
    // for existing customer, store the state of their login -- i.e OTP verified or not
    const [clientLoggedIn, setClientLoggedIn] = useState(false);
    // stores current client's data in this component
    const [client, setClient] = useState({});

    const [fullWidth] = React.useState(true);

    // for sending an SMS
    const {
        mutate: mutateSendSMS
    } = useMutation('sendSMS', api.sendSMS, {retry: 2});

    // for getting a client
    const {
        data: clientsData,
        mutate: mutateGetClients,
        isSuccess: mutateGetClientsSuccess,
        isError: mutateGetClientsError,
        reset: mutateGetClientsReset,
        isLoading: mutateGetClientsIsLoading,
    } = useMutation('getClients', api.getClient, {retry: 2});

    // whenever clientsData changes, set current client to the 0th client from results
    useEffect(() => {
        let currentClient;
        if (clientsData?.length > 0) {
            currentClient = clientsData[0]
        }
        // TODO set current client to something in the context -- if needed
        setClient(currentClient);
    }, [clientsData])

    // check if the phone number entered is the same as in the db
    const confirmPhone = () => {
        // client's phone number from db
        let dbPhone = ' ';
        // ensure current client is not undefined from previous steps
        if (client) {
            dbPhone = client.phoneNumber.trim();
        } else {
            alert('No client data, please refresh this page.')
        }

        // check equivalence
        if (dbPhone.trim() === inputs.phone.trim()) {
            // send the OTP to the number
            sendOTP().then(() => console.log("otp sent"))
                .catch(error => console.log("otp error", error.message));
            // proceed to otp modal
            changeCycle("otp");
        }
            // TODO: replace with proper error message
        // no match send error
        else alert('Client\'s phone number not on record, please refresh this page.')
    }

    // send OTP message
    const sendOTP = async () => {
        // generate random letters
        const id = generateRandomLetters(4);
        // generate random 4 digit OTP
        const code = generateRandomNumber(4);
        // update OTP state and display
        setOTP({id, code});

        // get phone number from info, and create message
        const params = {
            recipient: `${inputs.phone}`,
            message: `Dear customer, \n\nYour OTP for Bayport loan calculator is ${code}`
        }

        // send OTP message
        mutateSendSMS(params);
    }

    // confirm an OTP entered by user
    const verifyOTP = () => {

        if (inputs.otp.trim() === otp.code) {
            // successful, allow login
            setUser(client)
            setClientLoggedIn(true)
        }

        // incorrect otp, shpw error message
        else alert('Incorrect OTP, please try again.')
        // @TODO edit error showing as required
    }

    // get a clint's details form the database using their omang
    const getClientDetails = () => {
        // clear whatever is currently stored
        mutateGetClientsReset();
        // check db for client using omang
        mutateGetClients([{field: 'omang', value: inputs.omang}])

        // move to searching dialogue
        changeCycle('searching_client')
    }

    const changeCycle = value => {
        setStateCycle(value);
    };

    const toggleVisibilityExisting = () => {
        toggleInvisibleExisting();
        setIsOpen(false);
    };

    const toggleVisibilityNew = () => {
        toggleInvisibleNew();
        setMembershipProp(membership)
        setIsOpen(false);
        return (
            <Toast>
                <Toast.Header>
                    <img src="holder.js/20x20?text=%20" className="rounded me-2" alt=""/>
                    <strong className="me-auto">Bootstrap</strong>
                    <small>11 mins ago</small>
                </Toast.Header>
                <Toast.Body>Hello, world! This is a toast message.</Toast.Body>
            </Toast>
        )
    };

    const toggleModal = () => {
        setStateCycle("start");
        setIsOpen(!isOpen);
        setButtonVisible(false);
    };

    const modelComponents = () => {
        let components;
        switch (cycle) {
            case "start":
                components = (
                    <div className="otp-modal">
                        <DialogTitle id="alert-dialog-title">
                            <h1>Welcome</h1>
                        </DialogTitle>
                        <DialogContent>
                            <DialogContentText id="alert-dialog-description" className="dialogContent">
                                <p>Are you a new or an existing customer?</p>
                                <div className="radio-options">
                                      <span>
                                        <input
                                            type="radio"
                                            name="membership"
                                            value="no"
                                            onChange={e => setMembership(e.target.value)}
                                        />{" "}
                                          New
                                      </span>
                                    <span>
                                        <input
                                            type="radio"
                                            name="membership"
                                            value="yes"
                                            onChange={e => setMembership(e.target.value)}
                                        />{" "}
                                        Existing
                                      </span>
                                </div>
                            </DialogContentText>
                        </DialogContent>
                        <DialogActions>

                            <Button onClick={() => window.location.href = '/'}>
                                Cancel
                            </Button>

                            {
                                membership === "yes" ?
                                    <Button
                                        onClick={() => {

                                            changeCycle(membership);
                                        }}
                                    >
                                        Proceed
                                    </Button> :
                                    (
                                        membership === "no" &&
                                        <Button
                                            onClick={() => {
                                                toggleVisibilityNew();
                                            }}
                                        >
                                            Proceed
                                        </Button>
                                    )
                            }
                        </DialogActions>
                    </div>
                );
                break;
            case "no":
                components = (
                    <div className="otp-modal">
                        <div className="close" onClick={toggleModal}>
                            close
                        </div>
                        <div className="title">
                            <h1>Proceed</h1>
                        </div>
                        <div>
                            <p/>
                        </div>
                        <div className="proceed">
                            <Button onclick={() => toggleVisibilityNew()} label="Proceed"/>
                        </div>
                    </div>
                );
                break;
            case "yes":
                components = (
                    <>
                        <DialogTitle id="alert-dialog-title">
                            <h3>Verify your ID</h3>
                        </DialogTitle>
                        <DialogContent>
                            <DialogContentText id="alert-dialog-description">
                                <div>
                                    <Typography>Please enter your ID / Passport Number</Typography>
                                    <TextField
                                        type="text"
                                        id={"inputOmang"} name={"inputOmang"}
                                        placeholder="ID / Passport Number"
                                        onChange={(e) => {
                                            setInputs({...inputs, omang: `${e.target.value}`.trim()})
                                        }}/>
                                </div>
                            </DialogContentText>
                        </DialogContent>
                        <DialogActions>

                            <Button
                                onClick={() => changeCycle('start')}
                            >
                                Go back
                            </Button>
                            <Button
                                onClick={() => getClientDetails()}
                            >
                                Proceed
                            </Button>

                        </DialogActions>

                    </>
                );
                break;

            // while waiting for client details to arrive
            case "searching_client":
                components = (
                    <div>
                        <DialogContent>
                            <DialogContentText id="alert-dialog-description">
                                {mutateGetClientsError &&
                                <Typography>An error occurred, please refresh this page</Typography>
                                }

                                {
                                    mutateGetClientsIsLoading &&
                                    <div>
                                        <Typography>
                                            Loading ...
                                        </Typography>
                                    </div>

                                }

                                {(clientsData?.length > 0)
                                    ?
                                    // searching complete and clients data is not empty array
                                    changeCycle('confirm_no')

                                    :
                                    // no data there yet, check if successful
                                    (mutateGetClientsSuccess &&
                                        // successful query, client must not have been found
                                        <Typography>
                                            Omang not found. Please go back and check again, or restart as a new
                                            customer.
                                        </Typography>

                                    )

                                }

                            </DialogContentText>
                        </DialogContent>
                        <DialogActions>
                            {mutateGetClientsSuccess &&
                            <Button
                                onClick={() => changeCycle('yes')}
                            >
                                Go back
                            </Button>
                            }
                        </DialogActions>
                    </div>
                );
                break;

            case "confirm_no":
                components = (
                    <>
                        <DialogTitle id="alert-dialog-title">
                            <h3>Confirm your Number</h3>
                        </DialogTitle>
                        <DialogContent>
                            <DialogContentText id="alert-dialog-description">
                                <div>
                                    <Typography>
                                        Please enter the phone number we have on record: 7 *****
                                        <i>{clientsData[0].phoneNumber.slice(-2)}</i> to confirm that it is you.
                                    </Typography>
                                    <TextField
                                        type="text"
                                        placeholder="Phone Number"
                                        id={"inputPhone"} name={"inputPhone"}
                                        onChange={(e) => setInputs({
                                            ...inputs, phone: e.target.value
                                        })}/>

                                </div>
                            </DialogContentText>
                        </DialogContent>
                        <DialogActions>

                            <Button onClick={() => window.location.href = '/'}>
                                Cancel
                            </Button>

                            <Button
                                onClick={() => confirmPhone()}
                            >
                                Confirm
                            </Button>

                        </DialogActions>

                    </>
                );
                break;

            case "otp":
                components = (
                    <>
                        <DialogTitle id="alert-dialog-title">
                            <h3>OTP Verification Code</h3>
                        </DialogTitle>
                        <DialogContent>
                            <DialogContentText id="alert-dialog-description">
                                {!clientLoggedIn ?
                                    <div>
                                        <Typography>
                                            Please enter the verification code sent to {inputs.phone}
                                        </Typography>
                                        <TextField
                                            type="text" placeholder={`OTP Code here `}
                                            style={{
                                                margin: "15px",
                                                alignSelf: "center"
                                            }}
                                            onChange={(e) => setInputs(
                                                {...inputs, otp: e.target.value})}/>
                                    </div> :
                                    <div>
                                        <Typography>
                                            OTP verified successfully
                                        </Typography>
                                    </div>

                                }
                            </DialogContentText>
                        </DialogContent>
                        <DialogActions>
                            <Button onClick={() => window.location.href = '/'}>
                                Cancel
                            </Button>

                            {
                                !clientLoggedIn ?
                                    <Button
                                        onClick={() => verifyOTP()}
                                        style={{
                                            margin: "15px",
                                            alignSelf: "center"
                                        }}
                                    >
                                        Verify OTP
                                    </Button> : <Button
                                        onClick={() => {
                                            toggleVisibilityExisting();
                                            // reset login state in case of redo
                                            setClientLoggedIn(false);
                                        }}>
                                        Proceed
                                    </Button>
                            }

                        </DialogActions>

                    </>
                );
                break;
            default:
                components = <h1>Error</h1>;
                break;
        }

        return components;
    };

    const [isOpenNav, setIsOpenNav] = React.useState(false);

    const toggleSidebar = () => {
        setIsOpenNav(!isOpenNav);
    };

    function goHome() {
        history.push('/')
    }

    return (
        <>
            <header>
                <div className="logo">
                    {/* <h3>LOGO</h3> */}
                    <a href="/">
                        <img
                            src={bayportLogo}
                            style={{marginLeft: "50px", fontFamily:'Myriad Pro',}}
                            height="4rem"
                            onClick={() => goHome()}
                            alt=""
                        />
                    </a>
                </div>
                <div className="contact">
                    <Navbar toggleSidebar={toggleSidebar} goHome/>
                    <Sidebar isOpenNav={isOpenNav} toggleSidebar={toggleSidebar}/>
                </div>
            </header>
            <main
                style={{
                    backgroundImage: `url(${background})`,
                    backgroundPositionX: "40%"
                }}
            >
                <Card sx={{width: 10}} className='home-card'>
                    <h3>Welcome to Bayport</h3>
                    <h1>Shape your future now with a Bayport loan</h1>
                    {/*keep visible in case of cancel*/}

                    {(window.location.pathname === "/") ?
                        <div className="button-calculate" onClick={toggleModal}>
                            <p ref={applyButton}>GET A QUOTE </p>
                        </div> :
                        <div className="button-calculate" onClick={event => window.location.href = '/'}>
                            <p ref={applyButton}>Go Back</p>
                        </div>}

                    <br/>
                    <br/>
                </Card>

                <div className="description">
                    <div className="top-border">
                        <p>Quick Payment</p>
                    </div>
                    <div className="top-border">
                        <p>Competitive Interest</p>
                    </div>
                </div>
            </main>
            <Dialog
                open={isOpen}
                onRequestClose={toggleModal}
                contentLabel="My dialog"
                fullWidth={fullWidth}
                maxWidth={maxWidth}

            >
                {modelComponents()}
            </Dialog>
        </>
    );
}
